@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.light {
  --color-primary: #ffffff;
  --color-secondary: #1f2937;
  --color-text: #1f2937;
  --color-background: #ffffff;
  --color-card: #f3f4f6;
  --color-card-border: #e5e7eb;
}

.dark {
  --color-primary: #050816;
  --color-secondary: #aaa6c3;
  --color-text: #ffffff;
  --color-background: #050816;
  --color-card: #1f2937;
  --color-card-border: #374151;
  color-scheme: dark;
}

.violet-gradient {
  background: #804dee;
  background: linear-gradient(-90deg, #804dee 0%, rgba(60, 51, 80, 0) 100%);
  background: -webkit-linear-gradient(
    -90deg,
    #804dee 0%,
    rgba(60, 51, 80, 0) 100%
  );
}

.bg-primary {
  background-color: var(--color-background);
}

.text-primary {
  color: var(--color-text);
}

.text-secondary {
  color: var(--color-secondary);
}

.border-secondary {
  border-color: var(--color-secondary);
}

.bg-secondary {
  background-color: var(--color-secondary);
}

/* CountUp Animation */
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

.stat-card {
  transition: all 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.text-shadow-glow {
  text-shadow: 0 0 4px rgba(168, 85, 247, 0.4);
  transition: text-shadow 0.2s ease-in-out;
}

.number-glow {
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  background-image: linear-gradient(to right, #a855f7, #ec4899);
  position: relative;
  z-index: 1;
}

.number-glow::after {
  content: attr(data-content);
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  background-image: linear-gradient(to right, #a855f7, #ec4899);
  filter: blur(4px) brightness(1.3);
  opacity: 0.7;
}

.number-container {
  position: relative;
  display: inline-block;
  overflow: visible;
}

/* Smooth Scrolling */
html {
  scroll-behavior: smooth;
}

/* Adjust for fixed navbar */
#about, #skills {
  scroll-margin-top: 80px; /* Height of your navbar plus some padding */
}

@media (max-width: 768px) {
  #about, #skills {
    scroll-margin-top: 70px; /* Slightly less for mobile */
  }
}

/* Green glow animation for buttons */
@keyframes greenGlowPulse {
  0% {
    box-shadow: 0 0 0 0 rgba(34, 197, 94, 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(34, 197, 94, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(34, 197, 94, 0);
  }
}

.green-glow-button {
  position: relative;
  animation: greenGlowPulse 2s infinite;
}

.green-text {
  color: #22c55e;
  font-weight: 700;
} 